aside {
    .menu {
        color: #333;
        font-size: 18px;

        margin-bottom: 50px;
        li {
            margin-bottom: 15px;
            position: relative;
            &.subMenu > a {
                @include pseudo;
                &:after {
                    width: 10px;
                    height: 10px;
                    top: 50%;
                    margin-top: -5px;
                    right: -15px;
                    left: initial;
                    border: solid black;
                    transform: rotate(-45deg) scale(.6);
                    border-width: 0 2px 2px 0; } } }

        li ul {
            margin-top: 1em;
            margin-left: 1em;
            margin-right: 2em;
            //display: none
            li {
                margin-bottom: 15px;
                font-size: 16px;
                line-height: 16px; }
            a {
                color: #949494;
                &:hover {
                    color: #000; } } } }

    .smenu {
        color: #949494;
        font-size: 18px;
        margin-bottom: 50px;
        li {
            position: relative;
            margin-bottom: 15px;
            padding: 0;
            &.subMenu > a {
                @include pseudo;
                &:after {
                    width: 10px;
                    height: 10px;
                    top: 50%;
                    margin-top: -5px;
                    right: -15px;
                    left: initial;
                    border: solid #949494;
                    transform: rotate(-45deg) scale(.6);
                    border-width: 0 2px 2px 0; } } }
        a {
            color: #949494;
            transition: .25s ease;
            &:hover {
                color: #666; } }
        li ul {
            display: none; } } }
