main {
    margin-bottom: 1em;
    padding-bottom: 1em; }
.page__hero {
    @include clr;
    margin-bottom: 1em; }
.page__title {
    .title__viewAll {
        display: inline-block;
        opacity: .5;
        font-size: 40%;
        margin-left: 4%; } }

.page__catalog.row {
    @include clr;
    margin-bottom: 1em;
    .item {
        background: transparent;
        margin-left: 0px;
        margin-right: 0;
        margin-bottom: 10px;
        margin-top: 10px;
        padding: 15px;
        width: 33%;
        z-index: 1;
        position: relative;
        @include md {
            width: 48%; }
        @include sm {
            width: 100%; }

        &__row {
            min-height: 55px;
            display: block;
            width: 100%;
            margin-bottom: 15px;
            position: relative; }
        .img-block {
            //background: white;
            text-align: center;
            position: relative;
            width: 100%;
            //border: 5px solid #f4f4f4;
            //height: 300px;
            display: table;
            img {
                width: 100%;
                height: auto; } }
        &__actions {
            opacity: 0;
            transition: .25s ease;
            position: absolute;
            left: 0; right: 0;
            top: 50%;
            text-align: center;
            .btn {
                font-size: 16px;
                border: 1px solid #333;
                padding: 9px 18px;
                background: rgba(255, 255, 255, 0.6);
                width: 75%;
                &.btn-info {
                    background: #333333;
                    color: #fff; } } }
        &:hover {
            background: #fff;
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
            .item__actions {
                opacity: 1; } }
        &__type {
            position: absolute;
            bottom: 10px;
            left: 0;
            font-size: 10px;
            background: #eee;
            text-transform: uppercase;
            letter-spacing: .1em;
            padding: .5em .75em;

            &.hit {
                background: #949494;
                color: #fff; }
            &.new {
                background: #B09C6D;
                color: #fff; } }

        &__title {
            font-size: 12px;
            color: #949494;
            line-height: 14px;
            height: 16px;
            overflow: hidden;
            text-transform: uppercase;
            @include md {
                height: 30px; }
            a {
                color: #949494; } }
        &__price {
            &--sale {
                color: #F24B4B; } } } }



